import Head from 'next/head'
import Profile from '../components/Profile'
import fetch from 'isomorphic-unfetch'

import '../assets/scss/resume.scss';

const Resume = (props) => (
  <div>
    <Head>
      <meta name="viewport" content="initial-scale=1.0, width=device-width" />
      <title>Resume - {props.user.first_name} {props.user.last_name} - {props.user.headline}</title>
    </Head>
    <Profile user={props.user} profile={props.profile} />
  </div>
)

const fetchJson = async (url) => {
  return fetch(url).then(response => response.json())
}

Resume.getInitialProps = async ({ req }) => {
  const props = {}

  try {
    props.user = await fetchJson('https://remote.com/api/v1/public/user?slug=matheusbarros')
    props.profile = await fetchJson('https://remote.com/api/v1/profile/public/matheusbarros')
  } catch (e) {
    console.error(e)
  }

  return props
}

export default Resume
